.modal-overlay{position:fixed;top:0;left:0;right:0;bottom:0;background-color:rgba(0,0,0,.85);display:flex;justify-content:center;align-items:center;z-index:1000;padding:20px;margin-top:64px;backdrop-filter:blur(5px)}.modal-content{background:#fff;border-radius:16px;max-width:900px;width:100%;max-height:85vh;overflow-y:auto;position:relative;animation:modalFadeIn .4s cubic-bezier(0.4, 0, 0.2, 1);box-shadow:0 20px 40px rgba(0,0,0,.3);scrollbar-width:thin;scrollbar-color:#3498db #f1f1f1}.modal-content::-webkit-scrollbar{width:8px}.modal-content::-webkit-scrollbar-track{background:#f1f1f1;border-radius:4px}.modal-content::-webkit-scrollbar-thumb{background:#3498db;border-radius:4px}.modal-close{position:absolute;top:20px;right:20px;background:rgba(255,255,255,.9);border:none;width:32px;height:32px;border-radius:50%;font-size:20px;cursor:pointer;color:#333;z-index:1;transition:all .3s ease;display:flex;align-items:center;justify-content:center;box-shadow:0 2px 8px rgba(0,0,0,.1)}.modal-close:hover{background:#fff;transform:rotate(90deg);box-shadow:0 4px 12px rgba(0,0,0,.15)}.modal-header{position:relative;margin-bottom:0}.modal-header .modal-thumbnail{width:100%;height:350px;object-fit:cover;border-radius:16px 16px 0 0}.modal-header h2{position:absolute;bottom:0;left:0;right:0;padding:30px;margin:0;background:linear-gradient(transparent, rgba(0, 0, 0, 0.9));color:#fff;font-size:28px;font-weight:600;letter-spacing:.5px}.modal-body{padding:30px}.case-study-section{margin-bottom:35px}.case-study-section h3{color:#2c3e50;margin-bottom:20px;font-size:22px;font-weight:600;position:relative;padding-bottom:10px}.case-study-section h3:after{content:"";position:absolute;bottom:0;left:0;width:50px;height:3px;background:#3498db;border-radius:2px}.case-study-section p{line-height:1.8;color:#444;font-size:16px}.tech-stack{display:flex;flex-wrap:wrap;gap:12px;margin:20px 0}.tech-stack span{background:#f8f9fa;padding:8px 16px;border-radius:20px;font-size:.9em;color:#2c3e50;transition:all .3s ease;border:1px solid #e9ecef;font-weight:500}.tech-stack span:hover{background:#e9ecef;transform:translateY(-2px);box-shadow:0 4px 8px rgba(0,0,0,.1)}.architecture{font-style:italic;color:#666;margin:20px 0;padding:15px;background:#f8f9fa;border-radius:8px;border-left:4px solid #3498db}.challenges-list{list-style-type:none;padding:0}.challenges-list li{margin:12px 0;padding-left:25px;position:relative;line-height:1.6}.challenges-list li:before{content:"•";position:absolute;left:0;color:#3498db;font-size:20px;line-height:1}.metrics{display:flex;flex-wrap:wrap;gap:15px;margin:20px 0}.metrics .metric{background:linear-gradient(135deg, #3498db, #2980b9);color:#fff;padding:10px 20px;border-radius:25px;font-size:.95em;font-weight:500;animation:fadeIn .5s ease;box-shadow:0 4px 12px rgba(52,152,219,.2);transition:transform .3s ease}.metrics .metric:hover{transform:translateY(-2px);box-shadow:0 6px 15px rgba(52,152,219,.3)}.testimonial{background:linear-gradient(135deg, rgba(52, 152, 219, 0.1), rgba(41, 128, 185, 0.1));padding:25px;border-radius:12px;margin-top:25px;border:1px solid rgba(52,152,219,.2)}.testimonial p{font-style:italic;margin-bottom:15px;font-size:16px;line-height:1.8}.testimonial .client{font-size:.95em;color:#666;font-weight:500}.certifications{display:flex;flex-wrap:wrap;gap:12px}.certifications .certification{background:linear-gradient(135deg, #2ecc71, #27ae60);color:#fff;padding:8px 16px;border-radius:20px;font-size:.9em;font-weight:500;box-shadow:0 4px 12px rgba(46,204,113,.2);transition:transform .3s ease}.certifications .certification:hover{transform:translateY(-2px);box-shadow:0 6px 15px rgba(46,204,113,.3)}@keyframes modalFadeIn{from{opacity:0;transform:translateY(-30px)}to{opacity:1;transform:translateY(0)}}@media screen and (max-width: 768px){.modal-content{max-height:90vh;margin:0 15px}.modal-header .modal-thumbnail{height:250px}.modal-header h2{font-size:22px;padding:20px}.modal-body{padding:20px}.metrics{justify-content:center}.tech-stack{justify-content:center}.case-study-section h3{font-size:20px}}